* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-family: "Plus Jakarta Sans", sans-serif;
}

.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  max-width: 1024px;
  margin: 0 auto;
  position: relative;
  padding: 4px 16px;
  background: white;
}

.nav-logo img {
  width: 180px;
}

.nav-desktop-menu {
  display: flex;
  align-items: center;
  gap: 40px;
}
.nav-desktop-menu .nav-links {
  display: flex;
  gap: 32px;
  align-items: center;
  margin: 0;
}
.nav-desktop-menu .nav-links li {
  list-style: none;
}
.nav-desktop-menu .nav-links a {
  color: #000929;
  text-decoration: none;
}
.nav-desktop-menu .nav-links a:hover {
  color: #00796b;
  transition: 0.2s ease-in-out;
}

.nav-auth {
  display: flex;
  gap: 10px;
}
.nav-auth a {
  text-decoration: none;
}
.nav-auth-login {
  padding: 10px 30px;
  color: #00796b;
  transition: all 0.3s ease;
  font-weight: 600;
  border: 1px solid #00796b;
  border-radius: 8px;
}
.nav-auth-login:hover {
  border: 1px solid #000929;
}
.nav-auth-register {
  padding: 10px 20px;
  background: #00796b;
  color: white;
  transition: all 0.3s ease;
  font-weight: 600;
  border: 1px solid transparent;
  border-radius: 8px;
}
.nav-auth-register:hover {
  color: black;
  background: #e0def7;
}

.nav-toggle {
  display: none;
  font-size: 28px;
  cursor: pointer;
  z-index: 200;
}

.nav-mobile-menu {
  display: none;
  flex-direction: column;
  position: absolute;
  top: 100%;
  left: 0;
  width: 100%;
  background-color: white;
  padding: 24px;
  box-shadow: 0 8px 20px rgba(0, 0, 0, 0.1);
  z-index: 150;
  transition: all 0.3s ease;
}
.nav-mobile-menu.active {
  display: flex;
}
.nav-mobile-menu .nav-links-mobile {
  display: flex;
  flex-direction: column;
  gap: 14px;
  list-style-type: none;
  padding: 0 0 0 20px;
}
.nav-mobile-menu .nav-auth-mobile {
  display: flex;
  flex-direction: column;
  gap: 14px;
}
.nav-mobile-menu a {
  display: flex;
  justify-content: space-between;
  color: #000929;
  text-decoration: none;
  font-size: 18px;
  padding: 8px 0;
}
.nav-mobile-menu a:hover {
  color: #00796b;
}
.nav-mobile-menu .nav-auth-login {
  border: 2px solid #e0def7;
  border-radius: 8px;
  padding: 10px 20px;
  text-align: center;
}
.nav-mobile-menu .nav-auth-register {
  border: 2px solid #00796b;
  border-radius: 8px;
  padding: 10px 20px;
  background: #00796b;
  color: white;
  text-align: center;
}

@media (max-width: 768px) {
  .nav-links,
  .nav-auth {
    display: none !important;
  }
  .nav-toggle {
    display: block;
  }
}
.banner_wrapper {
  height: 100%;
}

.hero_wrapper {
  width: 100%;
  height: 87vh;
  background: #e5e5e5;
}
.hero_wrapper_content {
  max-width: 1024px;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: 100%;
}
.hero_wrapper_content h1 {
  max-width: 544px;
  font-weight: 700;
}
.hero_wrapper_content p {
  max-width: 450px;
  font-size: 18px;
  font-weight: 400;
  margin: 8px 0 0 0;
}
.hero_wrapper_content h1,
.hero_wrapper_content p {
  color: #000929;
}
.hero_wrapper_content span {
  color: #00796b;
  font-weight: 700;
  font-size: 32px;
}
.hero_wrapper_contentFlex {
  display: flex;
  gap: 80px;
  margin: 16px 0 0 0;
}

.searchForm input[type=radio] {
  display: none;
}

.radio-label {
  cursor: pointer;
  position: relative;
  padding-bottom: 4px;
  font-size: 18px;
  margin: 32px 0 16px 0;
}

.radio-label .radio-text {
  display: inline-block;
  padding: 5px 32px;
  transition: border-bottom 0.2s ease;
  border-bottom: 2px solid transparent;
}

.radio-label input[type=radio]:checked + .radio-text {
  border-bottom: 2px solid #009688;
  font-weight: bold;
  color: #00796b;
}

.form-group-flex {
  display: flex;
  position: relative;
}

.form-group {
  display: flex;
  flex-direction: column;
  background: white;
  padding: 25px 32px;
}
.form-group:first-of-type {
  border-top-left-radius: 8px;
  border-bottom-left-radius: 8px;
}

.form-group-submit {
  padding: 25px;
  display: flex;
  justify-content: center;
  align-items: center;
  background: white;
  border-top-right-radius: 8px;
  border-bottom-right-radius: 8px;
}

.form-group label {
  margin-bottom: 5px;
  font-weight: 600;
  color: #333;
}

.form-input {
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 6px;
  font-size: 16px;
  transition: border-color 0.2s ease;
  max-width: 150px;
}

.form-input:focus {
  border-color: #009688;
  outline: none;
}

.form-submit {
  padding: 12px 24px;
  background-color: #009688;
  color: white;
  font-weight: bold;
  border: none;
  border-radius: 6px;
  cursor: pointer;
  transition: background-color 0.2s ease;
}

.form-submit:hover {
  background-color: #00796b;
}

.suggestions {
  position: absolute;
  z-index: 10;
  left: 30px;
  bottom: 20px;
}

#suggestions ul li:hover {
  background-color: #f0f0f0;
}